.about {
  padding: 2rem 6rem; }
  .about > p {
    margin-bottom: 5rem;
    text-align: center; }
  .about h3 {
    text-align: center;
    padding: 3rem 0 2rem 0; }
  .about h3:before {
    content: "";
    position: absolute;
    left: calc(50vw - 6rem);
    height: 1px;
    width: 12rem;
    margin-top: -4rem;
    background-color: black; }
  .about h3 + p {
    margin-bottom: 3rem;
    text-align: center; }
  .about .team-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: space-evenly;
    -ms-flex-pack: space-evenly;
    justify-content: space-evenly;
    text-align: center; }
    .about .team-container img {
      border-radius: 50%;
      border: 3px solid transparent; }
    .about .team-container img:hover {
      border: 3px solid #a30606; }
    .about .team-container .person {
      padding: 0 1rem; }
      .about .team-container .person img,
      .about .team-container .person p {
        margin: 0.5rem 0; }
